
import RequireAuth from "@/components/RequireAuth";
import Nav from "@/components/Nav";
import BarChart from "@/components/chart/Chart";
import MobileBar from "@/components/MobileBar";
import { useEffect, useState } from "react";

export default function DashboardPage() {


  const [stats, setStats] = useState<any>(null);

  useEffect(() => {
    fetch("http://localhost:3003/api/content/stats") // ou juste /api/content/stats si le proxy est configur√©
      .then((res) => res.json())
      .then(setStats)
      .catch(console.error);
  }, []);

  if (!stats) return <p>Chargement des statistiques...</p>;
  return (
    // <RequireAuth>
    //   <Nav/>
    //   <MobileBar/>
      
    //   <div>
    //     <h1>Dashboard s√©curis√©</h1>
    //   </div>
    // </RequireAuth>

     <div className="p-6">
      <h1 className="text-3xl font-bold mb-6">üìä Dashboard des contenus</h1>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="bg-white p-6 shadow rounded-xl">
          <h2 className="text-xl font-semibold mb-2">Total des contenus</h2>
          <p className="text-4xl font-bold text-indigo-600">{stats.total}</p>
        </div>

        <div className="bg-white p-6 shadow rounded-xl">
          <h2 className="text-xl font-semibold mb-2">Contenus par statut</h2>
          <ul className="space-y-2">
            {stats.byStatus.map((item: any) => (
              <li key={item._id} className="flex justify-between">
                <span className="capitalize">{item._id}</span>
                <span className="font-bold">{item.count}</span>
              </li>
            ))}
          </ul>
        </div>

        <div className="bg-white p-6 shadow rounded-xl md:col-span-2">
          <h2 className="text-xl font-semibold mb-2">R√©partition par r√©seau social</h2>
          <ul className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-4">
            {stats.byPlatform.map((item: any) => (
              <li key={item._id} className="flex justify-between bg-gray-50 p-3 rounded shadow-sm">
                <span>{item._id}</span>
                <span className="font-bold">{item.count}</span>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
}



